import React, { useEffect } from 'react';
import logo from './logo.svg';
import './App.css';
import {
  ApolloClient,
  InMemoryCache,
  ApolloProvider,
  useQuery,
  gql
} from "@apollo/client";
function App() {
  
  const client = new ApolloClient({
    uri: 'https://bsc.streamingfast.io/subgraphs/name/pancakeswap/exchange-v2/graphql',
    cache: new InMemoryCache()
    
  });

  client
  .query({
    query: gql`
      query {
  pairs(first:1, where: {id: "0x07ddae60a99421eb948ae5f299117f8f8fe86a66"})
  {
    name,
    token0Price,
    token1Price,
    volumeToken0,
    volumeToken1,
    pairHourData(first: 24, orderDirection: asc)
    {
		
      hourlyVolumeToken0,
      hourlyVolumeToken1
    }
  }
}
    `,
    
  })
  .then(result => console.log(result));


  return (
    <div className="App">
      <header className="App-header">
      
      </header>
    </div>
  );
}

export default App;
